import Util from '../../js/util';

/**
 * ------------------------------------------------------------------------
 * Constants
 * ------------------------------------------------------------------------
 */

const tabs = [];

export const EventName = {
  HIDE: 'onHide',
  HIDDEN: 'onHidden',
  SHOW: 'onShow',
  SHOWN: 'onShown',
  CLICK_DATA_API: 'click',
  KEYDOWN_DATA_API: 'keydown',
  ON_REMOVE: 'onRemove'
};

const Attribute = {
  HIDDEN: 'hidden'
};

const ClassName = {
  DROPDOWN_MENU: 'dropdown-menu',
  ACTIVE: 'active',
  DISABLED: 'disabled',
  FADE: 'fade',
  SHOW: 'show'
};

export const Selector = {
  DROPDOWN: '.dropdown',
  NAV_LIST_GROUP: '.nav, .list-group, .tab-group',
  ACTIVE: '.active',
  ACTIVE_UL: 'li .active',
  DATA_MOUNT: '[data-mount="tab"]',
  DROPDOWN_TOGGLE: '.dropdown-toggle',
  DROPDOWN_ACTIVE_CHILD: '.dropdown-menu .active',
  BACK_TO_TABS: '[data-focus="back-to-tabs"]'
};

// Private

function _activate(element, container, callback) {
  let activeElements;

  if (container && (container.nodeName === 'UL' || container.nodeName === 'OL')) {
    activeElements = container.querySelector(Selector.ACTIVE_UL);
  } else {
    // make sure that any selected tab panel .active element is a direct descendant of the tab panel container
    activeElements = [].slice.call(container.children).filter(e => {
      return e.classList.contains(ClassName.ACTIVE);
    });
  }

  const active = activeElements[0];
  const isTransitioning = callback && (active && active.classList.contains(ClassName.FADE));
  const complete = () => _transitionComplete.bind(this)(
    element,
    active,
    callback
  );

  if (active && isTransitioning) {
    const transitionDuration = Util.getTransitionDurationFromElement(active);

    active.classList.remove(ClassName.SHOW);
    active.addEventListener(Util.TRANSITION_END, complete, { once: true });
    Util.emulateTransitionEnd(active, transitionDuration);
  } else {
    complete();
  }
}

function _transitionComplete(element, active, callback) {
  if (active) {
    active.classList.remove(ClassName.ACTIVE);

    const dropdownChild = active.parentNode.querySelector(
      Selector.DROPDOWN_ACTIVE_CHILD
    );

    if (dropdownChild) {
      dropdownChild[0].classList.remove(ClassName.ACTIVE);
    }

    if (active.getAttribute('role') === 'tab') {
      active.setAttribute('aria-selected', false);
      active.setAttribute('tabindex', '-1');
    } else if (active.getAttribute('role') === 'tabpanel') {
      active.hidden = true;
    }
  }

  element.classList.add(ClassName.ACTIVE);
  if (element.getAttribute('role') === 'tab') {
    element.setAttribute('aria-selected', true);
    element.setAttribute('tabindex', '0');
  } else if (element.getAttribute('role') === 'tabpanel') {
    element.removeAttribute(Attribute.HIDDEN);

    // Scroll back to top of panel if necessary
    const activePanelTop = element.getBoundingClientRect().top;
    const documentElementNode = document.documentElement;
    const documentScrollPaddingTop = documentElementNode.style.scrollPaddingTop ? parseInt(documentElementNode.style.scrollPaddingTop, 10) : 0;
    if (activePanelTop < 0) {
      const scrollOffset = activePanelTop - documentElementNode.getBoundingClientRect().top - documentScrollPaddingTop;
      window.scrollTo(0, scrollOffset);
    }

    if (this.backToTabs) {
      this.backToTabs.focusControls.remove();
      this.backToTabs.href = `#${element.id}-tab`;
      this.backToTabs.focusControls = new Util.FocusControls({ el: this.backToTabs });
    }
  }

  Util.reflow(element);

  if (element.classList.contains(ClassName.FADE)) {
    element.classList.add(ClassName.SHOW);
  }

  if (element.parentNode && element.parentNode.classList.contains(ClassName.DROPDOWN_MENU)) {
    const dropdownElement = element.closest(Selector.DROPDOWN)[0];

    if (dropdownElement) {
      const dropdownToggleList = [].slice.call(dropdownElement.querySelectorAll(Selector.DROPDOWN_TOGGLE));

      dropdownToggleList.classList.add(ClassName.ACTIVE);
    }

    element.setAttribute('aria-expanded', true);
  }

  if (callback) {
    callback();
  }
}

function _onKeycodeEvent(event) {
  const keycode = Util.getKeyCode(event);
  if (keycode === Util.keyCodes.SPACE || keycode === Util.keyCodes.ENTER) {
    event.preventDefault();
    this.show(event);
  } else if (keycode === Util.keyCodes.ARROW_LEFT) {
    if (this.isRTL) {
      _onKeycodeRight.bind(this)();
    } else {
      _onKeycodeLeft.bind(this)();
    }
  } else if (keycode === Util.keyCodes.ARROW_RIGHT) {
    if (this.isRTL) {
      _onKeycodeLeft.bind(this)();
    } else {
      _onKeycodeRight.bind(this)();
    }
  }
}

function _onKeycodeLeft() {
  if (this.tabIndex === 0) {
    return;
  }

  this.listNodeList[this.tabIndex - 1].focus();
}

function _onKeycodeRight() {
  if (this.tabIndex === this.listNodeList.length - 1) {
    return;
  }

  this.listNodeList[this.tabIndex + 1].focus();
}

/**
 * ------------------------------------------------------------------------
 * Class Definition
 * ------------------------------------------------------------------------
 */

class Tab {
  /**
   * Create a tab
   * @param {*} opts - The tab options
   * @param {Node} opts.el - The tab DOM node
   */
  constructor(opts) {
    this.el = opts.el;
    this.listGroup = this.el.closest(Selector.NAV_LIST_GROUP);
    this.isRTL = document.dir === 'rtl';
    this.backToTabs = document.querySelector(Selector.BACK_TO_TABS);

    // set back to tab href to active tab's id
    if (this.el.classList.contains(ClassName.ACTIVE) && this.backToTabs) {
      this.backToTabs.href = `#${this.el.id}`;
      this.backToTabs.focusControls = new Util.FocusControls({ el: this.backToTabs });
    }

    // prevents error if tab is not within a list group
    if (this.listGroup) {
      this.listNodeList = this.listGroup.querySelectorAll(`[data-mount=${this.el.dataset.mount}]`) || [];
      this.nodeListArray = [].slice.call(this.listNodeList);
      this.tabIndex = this.nodeListArray.indexOf(this.el);
    }

    // attach event listeners
    this.events = [
      { el: this.el, type: EventName.CLICK_DATA_API, handler: this.show.bind(this) },
      { el: this.el, type: EventName.KEYDOWN_DATA_API, handler: _onKeycodeEvent.bind(this) }
    ];

    // add event listeners
    Util.addEvents(this.events);

    tabs.push(this);

    // Create custom events
    this[EventName.ON_REMOVE] = new CustomEvent(EventName.ON_REMOVE, { bubbles: true, cancelable: true });
  }

  // Public

  /**
  * Shows a tab panel based on the tab clicked and hides other panels
  * @param {event} e event trigger
  */
  show(e) {
    if (e) {
      e.preventDefault();
    }

    if (this.el.parentNode &&
        this.el.parentNode.nodeType === Node.ELEMENT_NODE &&
        this.el.classList.contains(ClassName.ACTIVE) ||
        this.el.classList.contains(ClassName.DISABLED)) {
      return;
    }

    let target;
    let previous;
    const listElement = this.el.closest(Selector.NAV_LIST_GROUP);
    const selector = Util.getSelectorFromElement(this.el);

    if (listElement) {
      const itemSelector = listElement.nodeName === 'UL' || listElement.nodeName === 'OL' ? Selector.ACTIVE_UL : Selector.ACTIVE;
      previous = this.el.querySelector(itemSelector);
    }

    const hideEvent = new CustomEvent(EventName.HIDE, {
      detail: {
        relatedTarget: this.el
      }
    });

    const showEvent = new CustomEvent(EventName.SHOW, {
      detail: {
        relatedTarget: previous
      }
    });

    if (previous) {
      previous.dispatchEvent(hideEvent);
    }

    this.el.dispatchEvent(showEvent);

    if (showEvent.defaultPrevented ||
        hideEvent.defaultPrevented) {
      return;
    }

    if (selector) {
      target = document.querySelector(selector);
    }

    _activate.bind(this)(
      this.el,
      listElement
    );

    const complete = () => {
      const hiddenEvent = new CustomEvent(EventName.HIDDEN, {
        detail: {
          relatedTarget: this.el
        }
      });

      const shownEvent = new CustomEvent(EventName.SHOWN, {
        detail: {
          relatedTarget: previous
        }
      });

      if (previous) {
        previous.dispatchEvent(hiddenEvent);
      }

      this.el.dispatchEvent(shownEvent);
    };

    if (target) {
      _activate.bind(this)(target, target.parentNode, complete);
    } else {
      complete();
    }
  }

  /**
   * Remove event handlers.
   */
  remove() {
    Util.removeEvents(this.events);

    // remove this reference from array of instances
    const index = tabs.indexOf(this);
    tabs.splice(index, 1);
    this.el.dispatchEvent(this[EventName.ON_REMOVE]);
  }

  /**
   * Get instances.
   * @returns {Object[]} A array of tab instances
   */
  static getInstances() {
    return tabs;
  }
}

/**
 * ------------------------------------------------------------------------
 * Data Api implementation
 * ------------------------------------------------------------------------
 */
(() => {
  Util.initializeComponent(Selector.DATA_MOUNT, node => new Tab({ el: node }));
})();

export default Tab;
