const KeyboardUtil = {
  keyCodes: {
    ARROW_DOWN: 40,
    ARROW_UP: 38,
    ARROW_LEFT: 37,
    ARROW_RIGHT: 39,
    ENTER: 13,
    ESC: 27,
    SPACE: 32,
    BACKSPACE: 8,
    TAB: 9
  },
  getKeyCode(e) {
    return e.which || e.keyCode || 0;
  }
};

export default KeyboardUtil;
